  width: 124px;
  position: relative;
  line-height: 28px;
  font-weight: 700;
  font-family: inherit;
  display: inline-block;
}
.trendstaffmailru {
  width: 306px;
  display: inline-block;
}
.p13,
.trendstaffmailru {
  margin: 0;
  position: relative;
  line-height: 24px;
}
.contact-details,
.contacts {
  flex-direction: column;
  max-width: 100%;
}
.contact-details {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  gap: 4px;
  font-size: var(--font-size-base);
}
.contacts {
  width: 460px;
  border-radius: 24px;
  background-color: #fffefe;
  overflow: hidden;
  flex-shrink: 0;
  gap: var(--gap-xl);
}
.contacts,
.contacts-section,
.input-labels,
.map {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.map {
  border: 0;
  background-color: #d9d9d9;
  height: 320px;
  width: 620px;
  outline: 0;
  margin: 0 !important;
  position: absolute;
  top: 200px;
  left: 640px;
  flex-direction: column;
}
.contacts-section,
.input-labels {
  flex-direction: row;
  box-sizing: border-box;
  max-width: 100%;
}
.contacts-section {
  flex: 1;
  background-color: var(--color-deepskyblue-100);
  padding: 200px 180px;
  position: relative;
}
.input-labels {
  align-self: stretch;
  padding: 0 0 var(--padding-21xl);
  text-align: left;
  font-size: var(--font-size-37xl);
  color: var(--color-gray-100);
  font-family: var(--font-open-sans-hebrew);
}
.logo-icon1 {
  align-self: stretch;
  width: 113.3px;
  max-height: 100%;
  object-fit: cover;
  min-height: 85px;
}
.b3,
.logo-icon1,
.p14 {
  position: relative;
}
.b3 {
  width: 143px;
  font-weight: 700;
  display: inline-block;
}
.p14 {
  margin: 0;
  align-self: stretch;
  font-size: var(--font-size-base);
  color: var(--color-gray-100);
}
.title-and-subtitle {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-5xs);
  min-width: 196px;
}
.container1,
.logo-and-text {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  max-width: 100%;
}
.logo-and-text {
  flex: 1;
  flex-wrap: wrap;
  justify-content: flex-start;
  padding: 0 817px 0 0;
  box-sizing: border-box;
  gap: var(--gap-base);
}
.container1 {
  align-self: stretch;
  justify-content: center;
  font-size: var(--font-size-5xl);
  font-family: var(--font-open-sans-hebrew);
}
.footer-line {
  align-self: stretch;
  height: 1px;
  position: relative;
  border-top: 1px solid rgba(4, 5, 5, 0.32);
  box-sizing: border-box;
}
.div2 {
  width: 344px;
  position: relative;
  display: inline-block;
  max-width: 100%;
}
.input-labels1 {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: center;
  padding: 0 var(--padding-xl);
  box-sizing: border-box;
  max-width: 100%;
  color: var(--color-gray-100);
}
.asyama {
  position: relative;
}
.footer-content,
.input-labels2 {
  display: flex;
  align-items: flex-start;
}
.input-labels2 {
  align-self: stretch;
  flex-direction: row;
  justify-content: center;
  padding: 0 var(--padding-xl);
  color: rgba(4, 5, 5, 0.45);
}
.footer-content {
  width: 1248px;
  flex-direction: column;
  justify-content: flex-start;
  gap: 19.7px;
  max-width: 100%;
}
.outsourcing,
.register-button {
  display: flex;
  box-sizing: border-box;
}
.register-button {
  align-self: stretch;
  flex-direction: row;
  align-items: flex-start;
  justify-content: center;
  padding: 0 var(--padding-xl) 0 28px;
  max-width: 100%;
  text-align: left;
  font-size: var(--font-size-sm);
  color: #040505;
  font-family: var(--font-poppins);
}
.outsourcing {
  width: 100%;
  position: relative;
  background-color: var(--color-white);
  overflow: hidden;
  flex-direction: column;
  align-items: flex-end;
  justify-content: flex-start;
  padding: 0 0 var(--padding-21xl);
  line-height: normal;
  letter-spacing: normal;
}
@media screen and (max-width: 1200px) {
  .navbar {
    gap: 90px;
  }
  .employees-content {
    padding-top: var(--padding-59xl);
    padding-bottom: var(--padding-59xl);
    box-sizing: border-box;
  }
  .logo-and-text {
    padding-right: 408px;
    box-sizing: border-box;
  }
  .container1 {
    gap: 192px;
  }
}
@media screen and (max-width: 1050px) {
  .nav-links {
    display: none;
  }
  .h1 {
    font-size: 58px;
  }
  .hero-section {
    height: auto;
    min-height: 804;
  }
  .h11 {
